import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableRow,
  CButtonGroup,
  CButton,
} from "@coreui/react";
import { useParams, useHistory } from "react-router-dom";

import { Authentification } from "../../components/index";

import Loader from "../../components/Loader";
import Message from "../../components/Message";

import { getUsers, deleteUser } from "../../provider/userFetch";

const DetailClient = () => {
  /**Params Route */
  let { id } = useParams();
  let history = useHistory();

  /** State */
  const [client, setClient] = useState(null);
  const [error, setError] = useState(null);

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    async function fetch() {
      const resp = await getUsers(null, id);

      resp?.status ? setClient(resp?.data) : setError(resp?.error);
    }

    fetch();
  }, [id]);

  const back = () => {
    history.push(`/clients`);
  };

  const deleteFunction = async (id) => {
    const resp = await deleteUser(id);

    if (resp.status) {
      history.push(`/clients`);
    }
  };

  if (client === null) {
    return <Loader />;
  }

  if (error) {
    return <Message> {error}</Message>;
  }
  return (
    <>
      <Authentification />
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>Client {id}</strong>
                </CCol>
                <CCol sm={7}>
                  <CButtonGroup className="float-end me-3">
                    <CButton
                      color="outline-secondary"
                      className="mx-1"
                      onClick={() => back()}
                    >
                      Retour
                    </CButton>
                    <CButton
                      color="outline-danger"
                      className="mx-0"
                      onClick={() => deleteFunction(client?._id)}
                    >
                      Suppprimer
                    </CButton>
                  </CButtonGroup>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CTable>
                <CTableBody>
                  <CTableRow>
                    <CTableDataCell>firstName</CTableDataCell>
                    <CTableDataCell>{client.firstName}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>lastName</CTableDataCell>
                    <CTableDataCell>{client.lastName}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>email</CTableDataCell>
                    <CTableDataCell>{client.email}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Creation</CTableDataCell>
                    <CTableDataCell>{client.createdAt}</CTableDataCell>
                  </CTableRow>
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};

export default DetailClient;
